:root {
  --main-color: #082841;
  --secondary-color: #bd782a;
  --section-margin-top: 75px;
  --section-margin-bottom: 150px;
}
::selection {
  background-color: var(--main-color);
  color: var(--secondary-color);
}
* {
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
/* start section name css */
.section-name {
  margin-top: var(--section-margin-top);
  margin-bottom: 30px;
}
.section-name h1 {
  color: var(--main-color);
}

.section-name h1 span {
  color: var(--secondary-color);
}
/* end section name css */

/*start header css */
.header .main {
  height: 80vh;
  background-image: url("../images/frontcover.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  margin-top: var(--section-margin-top);
  margin-bottom: var(--section-margin-bottom);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .main::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgb(0%, 0%, 0%, 60%);
}
.header .main .text {
  position: relative;
  z-index: 2;
  color: white;
  text-align: center;
}

.header .main .text .small {
  font-size: 50px;
}
.header .main .text .big {
  font-size: 80px;
  color: var(--secondary-color);
  font-weight: 500;
}

@media only screen and (max-width: 700px) {
  .header .main .text .small {
    font-size: 30px;
  }
  .header .main .text .big {
    font-size: 60px;
    color: var(--secondary-color);
  }
}

/*end header css */

/* start about css */
.about {
  margin-bottom: var(--section-margin-bottom);
}
.about .about-content {
  font-size: 20px;
  color: white;
  border: white solid 1px;
  border-radius: 8px;
  box-shadow: var(--secondary-color) 0px 0px 6px 1px;
  background-color: var(--main-color);
}
.about .about-content h5 {
  margin-bottom: 25px;
}

@media only screen and (max-width: 700px) {
  .about .about-content {
    font-size: 18px;
  }
}
/* end about css */

/* start values css */
.values {
  margin-bottom: var(--section-margin-bottom);
  background-color: var(--main-color);
}
.values .row {
  padding-top: 70px;
  padding-bottom: 70px;
  margin-left: 5px;
}
.values .row .value {
  margin-top: 20px;
  background-color: white;
  color: var(--main-color);
  border: solid white 1px;
  border-radius: 6px;
  padding: 10px;
  height: 300px;
}
@media only screen and (max-width: 800px) {
  .values .row .value {
    height: fit-content;
    padding: 10px;
  }
}
.values .container {
  position: relative;
  border-left: 8px solid var(--secondary-color);
  padding: 30px;
}
.values .container::before {
  content: "";
  position: absolute;
  left: 8px;
  top: 0;
  height: 100%;
  width: 8px;
  background-color: var(--secondary-color);
}
.values .container::after {
  content: "";
  position: absolute;
  left: 24px;
  top: 0;
  height: 100%;
  width: 8px;
  background-color: var(--secondary-color);
}

.values .row .value h2 {
  padding-bottom: 5px;
  color: var(--secondary-color);
  border-bottom: 1px black solid;
}
/* end values css */

/* start expertise css */
.expertise {
  margin-bottom: var(--section-margin-bottom);
  background-image: url("../images/expertise1.jpg");
  background-size: cover;
  background-position: center;
  height: 120vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.expertise::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0, 80%);
}
.expertise .container {
  position: relative;
  z-index: 2;
}

.expertise .technical,
.expertise .supervision {
  text-align: center;
}
.expertise .title h1 {
  color: var(--secondary-color);
  font-weight: 600;
}
.expertise .title ul li {
  color: white;
  font-size: 25px;
  line-height: 1.5em;
}
/* end expertise css */

/* start service css */
.service {
  margin-bottom: var(--section-margin-bottom);
  background-color: var(--main-color);
}
.service .row {
  margin: 10px;
}

.service img {
  height: 300px;
  width: 100%;
}
.service .container .row h3 {
  color: var(--secondary-color);
  font-weight: 600;
}

.service .container .row .top-card {
  margin-top: 4px;
  padding-top: 0px;
  margin-bottom: 20px;
  overflow: hidden;
}
.service .container .row .top-card:hover {
  transform: rotate(2deg) scale(1, 1);
  opacity: 0.7;
}

.service .container .row .down-card {
  margin-top: 60px;
  margin-bottom: 4px;
  padding-bottom: 0px;
  padding: 10px 4px;
  height: 360px;
}

.service .container .row .down-card,
.service .container .row .top-card {
  border-radius: 6px;
  box-shadow: white 0px 0px 3px 1px;
  background-color: white;
}

.service .container .row .list-group-item {
  border: none;
}

.service .container {
  position: relative;
  border-left: 8px solid #bd782a;
  padding: 30px;
}
.service .container::before {
  content: "";
  position: absolute;
  left: 8px;
  top: 0;
  height: 100%;
  width: 8px;
  background-color: var(--secondary-color);
}
.service .container::after {
  content: "";
  position: absolute;
  left: 24px;
  top: 0;
  height: 100%;
  width: 8px;
  background-color: var(--secondary-color);
}

@media only screen and (max-width: 900px) {
  .service .container .row .top-card {
    padding-top: 0px;
    margin: 10px 30px;
  }

  .service .container .row .down-card {
    margin: 10px 30px;
    padding-bottom: 0px;
  }
}
/* end service css */

/* start types of project css */
.types {
  text-align: center;
  margin-bottom: var(--section-margin-bottom);
}
.types .content {
  border: 1px solid var(--main-color);
  display: inline-block;
  padding: 20px;
  border-radius: 10px;
  box-shadow: var(--main-color) 0px 0px 4px 1px;
  width: 400px;
}
.types .content h4 {
  border-bottom: 1px solid var(--secondary-color);

  margin-bottom: 15px;
  padding-bottom: 8px;
}

@media only screen and (max-width: 600px) {
  .types .content {
    width: 310px;
  }
}
@media only screen and (max-width: 320px) {
  .types .content {
    width: 240px;
  }
}
/* end types of project css */

/*start projects css */
.project-item {
  margin-bottom: 40px;
}
.projects {
  margin-bottom: var(--section-margin-bottom);
  background-color: var(--main-color);
  padding-top: 80px;
  padding-bottom: 80px;
}
.projects .project-item .item {
  border-radius: 6px;
  box-shadow: #bd782a 0px 0px 8px 2px;
  overflow: hidden;
  padding: 5px;
  background-color: var(--main-color);
}

.project-item img {
  height: 300px;
}

.project-item .item p strong {
  font-size: 20px;
  color: var(--secondary-color);
}
.project-item .item p span {
  font-size: 20px;
  color: white;
}
.project-item .item p {
  margin-bottom: 10px;
}

@media only screen and (max-width: 1200px) and (min-width: 100px) {
  .project-item .item p strong {
    font-size: 13px;
  }
  .project-item .item p span {
    font-size: 13px;
  }
  .project-item .scope-types {
    font-size: 11px;
  }
}
/* start contact css */
.contact {
  margin-top: 20px;
  background-color: #082841;
  padding-top: 0px;
  padding-bottom: 40px;
}
.contact img {
  width: 100%;
  height: 320px;
}
.contact .contact-image {
  margin-top: 30px;
  overflow: hidden;
  height: 320px;
  box-shadow: white 0px 0px 3px 2px;
  border-radius: 6px;
  width: 100%;
}
.contact .ways-contact {
  margin-top: 30px;
  padding: 0px 20px;
  width: 100%;
}
.contact .ways-contact span {
  display: inline-block;
}
.contact .ways-contact .info,
.contact .ways-contact .number {
  margin-bottom: 10px;
  font-size: 18px;
  color: white;
}
.contact .ways-contact .info-number svg {
  margin-right: 10px;
  padding-top: 2px;
  color: var(--secondary-color);
  font-size: 22px;
}

.contact .ways-contact .social a {
  color: var(--secondary-color);
  margin-right: 40px;
  font-size: 22px;
}

.contact .logo-contact {
  margin-top: 30px;
  display: flex;
  height: 90%;
  width: 100%;
}
.contact .logo-contact .logo {
  align-self: flex-end;
  justify-self: flex-end;
}
.contact .logo-contact img {
  width: 70%;
  height: fit-content;
}

@media only screen and (max-width: 485px) {
  .contact .contact-image {
    width: 80%;
    margin-left: 40px;
  }
  .contact .ways-contact {
    margin-left: 20px;
  }
  .contact .logo-contact {
    height: 66%;
    text-align: center;
  }
  .contact .logo-contact img {
    width: 47%;
  }
}

@media only screen and (min-width: 486px) and (max-width: 800px) {
  .contact .contact-image {
    width: 80%;
    margin-left: 50px;
  }
  .contact .ways-contact .info,
  .contact .ways-contact .number {
    font-size: 16px;
  }
  .contact .ways-contact .info-number svg {
    font-size: 18px;
    margin-right: 2px;
  }
  .contact .ways-contact .social svg {
    font-size: 18px;
  }
  .contact .ways-contact {
    padding-left: 50px;
  }
  .contact .logo-contact img {
    width: 40%;
  }
  .contact .logo-contact {
    height: 60%;
    text-align: center;
  }
}

@media only screen and (min-width: 801px) and (max-width: 900px) {
  .contact .contact-image {
    width: 100%;
  }
  .contact .ways-contact {
    padding: 0px 5px;
    margin-left: 30px;
  }
  .contact .logo-contact {
    height: 90%;
  }
  .contact .logo-contact .logo {
    align-self: flex-end;
    justify-self: flex-end;
  }
  .contact .logo-contact img {
    width: 100%;
  }
}
/* end contact css */
