.my-navbar nav {
  background-color: #082841;
  position: fixed;
  width: 100%;
  z-index: 99999;
  top: 0;
}

.my-navbar .navbar-light .nav-link {
  color: white;
  padding-bottom: 6px;
}

.nav > li > a:focus,
.my-navbar .navbar-light .nav-link:hover {
  text-decoration: none;
  color: #bd782a;
  background-color: transparent !important;
}
/* Menu */
.my-navbar .navbar-light .nav-link:after {
  display: block;
  content: "";
  border-bottom: solid 2px #bd782a;
  transform: scaleX(0);
  transition: transform 300ms ease-in-out;
}

.my-navbar .navbar-light .nav-link:hover:after {
  transform: scaleX(1);
}

.my-navbar .navbar-light .navbar-toggler {
  background-color: #bd782a;
}

.my-navbar .navbar-light .navbar-toggler {
  color: #082841;
  border-color: rgba(0, 0, 0, 0.1);
}
